.btn-primary {//primary button
    outline: none;
    appearance: none;
    background-color: $primary-500;
    color: #fff;
    border: 1.5px solid $primary-500;
    border-radius: 5px;
    padding: .375rem .75rem;
    transition: box-shadow 300ms ease-out;
    width: fit-content;
   cursor: pointer;
    &:focus{
        box-shadow: 0 0 2px 3px $primary-800;
        transition: box-shadow 300ms ease-out;
    }
svg{
    margin-right: 0.5rem;
}
    
}
::file-selector-button{
    outline: none;
    appearance: none;
    background-color: $primary-500;
    color: #fff;
    border: 1.5px solid $primary-500;
    border-radius: 5px;
    padding: .375rem .75rem;
    transition: box-shadow 300ms ease-out;
}
.btn-secondary{
    background-color: white;
    color: $primary;
}
a.btn-primary{
    
    text-decoration: none;
}

.btn-delete{
    background-color: $error-response;
    border-color: $error-response;
    &:focus{
        box-shadow: 0 0 2px 3px $error-response;
        transition: box-shadow 300ms ease-out;
    }
}

.close{//wrapper for close button
    display: flex;
    justify-content: flex-end;
    margin-block: $spacer-2;
}
.btn-close{//close button
   background: transparent url("../../assets/img/icons/close.svg") center/1.5em auto no-repeat;
    border: 0;
    border-radius: .375rem;
    padding: .25em .25em;
    width: 2rem;
    height: 2rem;
    cursor: pointer;
    opacity: .5;
    &:hover{
        opacity: 1;
    }
}
